
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'

import { mapGetters, mapActions } from "vuex";
export default {
components: {
Swipeout,
SwipeoutItem,
SwipeoutButton,
GroupTitle
},
data() {
return {
show: false
};
},
methods: {
...mapActions(["setIsPost", "delPostsById"]),
handleDlePost(id) {
this.delPostsById({ id });
}
},
computed: {
...mapGetters(["getFilterPosts", "getTab", "getIsPost"]),
postLists() {
return this.getFilterPosts(this.tab);
},
tab() {
return this.getTab;
},
isPost() {
return this.getIsPost;
}
},
beforeRouteEnter(to, from, next) {
next(vm => {
vm.setIsPost({
isPost: true
});
});
},
beforeRouteLeave(to, from, next) {
this.setIsPost({
isPost: false
});
next();
}
};
